@import "normalize";
@import "typesetting/typesetting";

html {
    font-family: $font-family-primary;
}

body {
    @include set-text-style($text-style-5);
}

h1 {
    @include set-text-style($text-style-1);
}

h2 {
    @include set-text-style($text-style-2);
}

h3 {
    @include set-text-style($text-style-3);
}

h4 {
    @include set-text-style($text-style-4);
}

pre {
    // @include set-text-style($text-style-5);
    font-family: $font-family-monospace;
}

code {
    // @include set-text-style($text-style-5);
    color: $color-text-secondary;
    font-family: $font-family-monospace;
}

b,
strong {
    font-weight: $font-weight-bold;
}

i, 
em {

}

a {

}

a:hover,
a:active {

}

mark {
    // @include set-text-style($text-style-5);
    background-color: $color-text-highlighted-background;
}

small {
    font-size: $font-size-small;
}

